<template>
    <div class="safe-area">
        <!-- CustomHeader -->
        <router-link :to="{ name: 'EditProfile', params: { userData } }">
            <button class="edit-button">
                <i class="fa fa-edit edit-btn"></i>
            </button>
        </router-link>
        <div class="profile-container">
            <img :src="userData && userData.media ? userData.media.display_url : '/assets/images/default_profile.png'"
                alt="Profile Image" class="profile-image" />
            <h1 class="title">Profile</h1>
            <div v-if="userData" class="data-profile-container">
                <div class="info-container">
                    <span class="label">Name:</span>
                    <span class="value">{{ userData.surname }} {{ userData.first_name }} {{ userData.last_name }}</span>
                </div>
                <div class="info-container">
                    <span class="label">Username:</span>
                    <span class="value">{{ userData.username }}</span>
                </div>
                <div class="info-container">
                    <span class="label">Email:</span>
                    <span class="value">{{ userData.email }}</span>
                </div>
                <div class="info-container">
                    <span class="label">Jabatan:</span>
                    <span class="value">{{ userData.jabatan.name }}</span>
                </div>
                <div v-if="documents && documents.length" class="documents-container">
                    <div v-for="(document, index) in documents" :key="index" class="info-container"
                        @click="openDocument(document)">
                        <span class="document-label">{{ document.description }}:</span>
                        <i class="fa fa-chevron-right"></i>
                    </div>
                </div>
                <div class="logout-button-container">
                    <button @click="handleLogout">Logout</button>
                </div>
            </div>
        </div>
        <!-- PDF Viewer Modal -->
        <div v-if="showPdfViewer" class="pdf-viewer-modal">
            <button @click="closePdfViewer">Close</button>
            <PDFViewer v-if="pdfUrl" :source="pdfUrl" @download="handleDownload" />
        </div>
    </div>
</template>

<script>
import axios from '@/utils/axios';
import { ref, onMounted } from 'vue';
// import PDFViewer from 'pdf-viewer-vue'
import PDFViewer from 'pdf-viewer-vue/dist/vue2-pdf-viewer'

export default {
    components: {
        PDFViewer
    },
    setup() {
        const userData = ref(null);
        const documents = ref([]);
        const showPdfViewer = ref(false);
        const pdfUrl = ref(null);
        const loading = ref(true);

        const handleLogout = async () => {
            loading.value = true;
            try {
                await axios.post('/logout');
                localStorage.clear();
                location.replace('/login');
            } catch (error) {
                console.error("Logout failed:", error);
            } finally {
                loading.value = false;
            }
        };

        const fetchUserData = async () => {
            try {
                const response = await axios.get('/user');
                userData.value = response.data;
            } catch (error) {
                console.error('Error fetching user data:', error);
            } finally {
                loading.value = false;
            }
        };

        const fetchDocuments = async () => {
            try {
                const response = await axios.get('/documents');
                documents.value = response.data;
            } catch (error) {
                console.error('Error fetching documents:', error);
            }
        };
        const openDocument = (document) => {
            pdfUrl.value = `https://ac.igm.biz.id/public/uploads/documents/${document.name}`;
            showPdfViewer.value = true;
        };
        const closePdfViewer = () => {
            showPdfViewer.value = false;
            pdfUrl.value = null;
        };
        const handleDownload = ({ src, filename }) => {
            const link = document.createElement('a');
            link.href = src;
            link.download = filename;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            console.log(src, filename)
        };

        onMounted(() => {
            fetchUserData();
            fetchDocuments();
        });

        return {
            userData,
            documents,
            loading,
            handleLogout,
            showPdfViewer,
            pdfUrl,
            openDocument,
            closePdfViewer,
            handleDownload
        };
    }
};
</script>

<style scoped>
.safe-area {
    display: flex;
    flex-direction: column;
    background-color: #f0f4f8;
    padding: 20px;
    position: relative;
    min-height: 100vh;
}

.loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
}

.profile-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
}

.profile-image {
    width: 50%;
    height: auto;
    border-radius: 50%;
    margin-bottom: 20px;
}

.title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
}

.data-profile-container {
    width: 90%;
}

.info-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 2em;
    cursor: pointer;
}

.label {
    font-size: 18px;
    font-weight: bold;
    color: #666;
}

.value {
    font-size: 18px;
    color: #333;
}

.logout-button-container {
    text-align: center;
}

.logout-button-container button {
    background-color: rgb(170, 12, 12);
    color: #f0f4f8;
    border-radius: 7px;
    padding: 1em;
}

.edit-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    cursor: pointer;
}

.edit-btn {
    font-size: 24px;
    color: #000;
}

.documents-container {
    width: 100%;
    margin-top: 20px;
    color: rgb(37, 48, 210);
}

.document-label {
    font-size: 18px;
    color: #4520b3;
}

.documents-container {
    margin-top: 20px;
}

.documents-container ul {
    list-style-type: none;
    padding: 0;
}

.documents-container li {
    margin-bottom: 10px;
}

.documents-container button {
    background: none;
    border: none;
    color: blue;
    cursor: pointer;
    text-decoration: underline;
}

/* Modal styles */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    position: relative;
}

.modal-content .close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
}

.pdf-viewer-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100vh - 30px);
    margin-top: 30px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pdf-viewer-modal button {
    position: absolute;
    top: -20px;
    right: 20px;
    background: white;
    border: none;
    font-size: 20px;
    color: rgb(225, 27, 27);
    cursor: pointer;
}
</style>
